



























































import {mapState,mapMutations} from 'vuex' // 想获取Vuex全局的值，就得在对应页面引入vuex的mapState状态集合
export default {
	data() {
		return {
			list:[
				{
					'name':'我的收藏',
					'img':'../../static/xing.png'
				},
				{
					'name':'收货地址管理',
					'img':'../../static/addr.png',
				},
				{
					'name':'使用帮助',
					'img':'../../static/help.png'
				},
				{
					'name':'辅助设置',
					'img':'../../static/settings.png'
				},
				{
					'name':'清理缓存',
					'img':'../../static/ava_clear.png'
				},
				{
					'name':'退出登录',
					'img':'../../static/exit.png'
				}
			],
			myPhoto:'',
			tempFilePath:'',
			savedFilePath:'',
			showMask: false,
			item:'',
			loginInfo:'请登录',
			nickName:'mir王',
			show:true,
			hide:false
		}
	},
	computed: mapState(['hasLogin', 'username', 'avatar']), // 获取Vuex中state的状态值
	onLoad(e) { // 监听页面加载
		this.tempFilePath = '';
		this.savedFilePath = uni.getStorageSync('savedFilePath'); //缓存的图片根据key值savedFilePath来取对应的value
		this.getPhoto();
		this.wxPhoto();
	}, 
	onShow(){
		this.wxPhoto();
		this.item = uni.getStorageSync('concent'); 
		this.loginState();
	},
	methods: { 
		...mapMutations(['logout']), // ...mapMutations是扩展运算符
		wxPhoto(){
			if(this.avatar){
				this.myPhoto = this.avatar; // 微信登录的头像
			}
		},
		goLogin(){
			if(this.hasLogin){ // 如果登录信息存在
				return;
			}else{
				uni.navigateTo({
					url:'../login/login'
				})
			}
		},
		goPeisong(){
			let photo = '';
			if(this.tempFilePath != ''){
				photo = this.tempFilePath;
			}else if(this.tempFilePath == '' && this.savedFilePath != ''){
				photo = this.savedFilePath;
			}
			uni.navigateTo({
				url:'../mine/send/send?myPhoto='+photo+''
			})
		},
		// 判断登录状态
		loginState(){
			if(this.hasLogin){ // 如果登录信息存在
				this.loginInfo = '登录信息';
				this.hide = true;
			}else if(!this.hasLogin){
				this.loginInfo = '请登录';
				this.hide = false;
			}
		},
		goUpdateinfo(){
			uni.navigateTo({
				url:'../mine/updateInfo/updateInfo?nickName='+this.nickName+''
			})
		},
		getPhoto(){
			if(this.tempFilePath != ''){
				this.myPhoto = this.tempFilePath;
			}else if(this.tempFilePath === '' && this.savedFilePath != ''){
				this.myPhoto = this.savedFilePath;
			}else{
				this.myPhoto = '../../static/photo.png';
			}
		},
		clear() {
			uni.setStorage('savedFilePath', '');
			uni.clearStorage();
			this.tempFilePath = '';
			this.savedFilePath = '';
			this.myPhoto = '';
			this.getPhoto();
		},
		uploadFile(){
			var _this = this;
			uni.chooseImage({ 
				count:1,//默认是9
				sizeType:['compressed'],//压缩图
				sourceType:['album'],//相册
				success: (res) => {
					_this.tempFilePath=res.tempFilePaths[0];// 获取上传的相片路径
					_this.saveFile();
					_this.getPhoto();
				}
			})
		},
		saveFile(){
			var _this = this;
			uni.setStorageSync('savedFilePath',_this.tempFilePath);// 缓存图片,key:savedFilePath,value:_this.tempFilePath
			uni.showToast({
				title: '保存成功',
				duration:1000
			})
		},
		navigateTo(){
			uni.navigateTo({
				url:'items/items'
			})
		},
		go_page(index){
			switch(index){
				case 0:
					uni.navigateTo({
						url:'../../platforms/app-plus/tabbar/tabbar'
					})
				break;
				case 1:
					uni.navigateTo({
						url:'addr_gl/addr_gl'
					})
				break;
				case 2: 
				break;
				case 3: 
					uni.navigateTo({
						url:'sette/sette'
					})
				break;
				case 4:
					this.clear();
				break;
				case 5:
					let _this = this;
					uni.showModal({
						title:'测试APP提示：',
						content:'确定退出吗？',
						success:(res) => {
							if(res.confirm){
								// 上面通过...mapMutations(['logout']),这个方法将Mutations的logout方法加载进来，然后这里通过this.logout()就可以请求到对应的vuex仓库里的方法
								_this.logout();
								_this.username = '';
								uni.navigateTo({
									url:'../login/login'
								})
							}else if(res.cancel){
								console.log('用户点击取消');
							}
						}
					})
				break;
			}
		},
	},
	components: {
	}
}



